.contacts {
  width: 100%;
}

.contacts__wrapper {
  padding: 45px 0 39px;
  margin: 0 auto;
  max-width: 425px;
}

.contacts__title {
  padding: 0;
  margin: 0 30px 28px;

  @include font-face(700, 25px, 30px);
  color: $black;
}

.contacts__list {
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0 30px 30px;
  min-height: 90px;

  @include font-face(400, 14px, 30px);
}

.contacts__item {
  display: flex;
  margin-bottom: 30px;
}

.contacts__name {
  width: 70px;
  margin-right: 7px;
}

.contacts__value {
  width: 183px;
  padding: 0;
  margin: 0;
}

.contacts__value a {
  position: relative;
  text-decoration: none;
  color: $g-black;

  &::before {
    content: "";
    position: absolute;
    bottom: -4px;
    width: 170px;
    height: 1px;
    background-color: $n-green;
  }

  &:hover {
    color: $dd-green;

    &::before {
      background-color: $dd-green;
    }
  }

  &:active {
    &::before {
      opacity: 0.1;
    }
  }
}

.contacts__map {
  position: relative;
  width: 100%;
  height: 454px;
  margin-bottom: 43px;
  background-color: $w-gray;

  &::before {
    content: "";
    position: absolute;
    z-index: 2;
    top: 32%;
    left: 43%;
    width: 67px;
    height: 100px;
    background-image: url("../img/index/contacts/map-pin.svg");
    background-repeat: no-repeat;
  }
}

.contacts__map iframe {
  position: absolute;
  z-index: 3;
  width: 100%;
  height: 454px;
  border: none;
}

.contacts__map img {
  position: absolute;
  top: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
}

.contacts__button-link {
  margin-left: 30px;
  margin-right: 30px;

  @include font-face(700, 18px, 20px);
}

@media (min-width: $tablet-width) {
  .contacts__wrapper {
    position: relative;
    display: grid;
    grid-template-columns: 50px 21.75% 21.75% 21.75% 21.75% 50px;
    grid-template-rows: auto auto auto auto;
    padding: 76px 0 55px;
    min-width: 768px;

    &::before {
      content: "";
      position: absolute;
      top: 53px;
      right: 53px;
      width: 47px;
      height: 81px;
      background-image: url("../img/index/contacts/flag.svg");
      background-repeat: no-repeat;
    }
  }

  .contacts__title {
    grid-column: 2 / 4;
    grid-row: 1 / 2;
    margin: 0;
    margin-bottom: 49px;

    @include font-face(700, 43px, 50px);
  }

  .contacts__list {
    grid-column: 2 / -2;
    grid-row: 2 / 3;
    flex-direction: row;
    margin: 0;
    margin-bottom: 17px;

    @include font-face(400, 17px, 30px);
  }

  .contacts__item {
    &:first-child {
      margin-right: 37px;
    }
  }

  .contacts__map {
    grid-column: 1 / -1;
    grid-row: 3 / 4;
    min-width: 768px;
    min-height: 457px;
    margin-bottom: 57px;

    &::before {
      top: 139px;
      left: 363px;
    }
  }

  .contacts__value {
    width: 220px;
    padding: 0;
    margin: 0;
  }

  .contacts__value a {
    &::before {
      width: 206px;
    }
  }

  .contacts__button-link {
    justify-self: center;
    grid-column: 3 / 5;
    grid-row: -1 / -2;
    margin-left: 0;
    margin-right: 0;
    width: 206px;

    @include font-face(700, 17px, 20px);
  }
}

@media (min-width: $desktop-width) {
  .contacts__wrapper {
    grid-template-columns: 16.67% 16.67% 16.67% 16.67% 16.67% 16.67%;
    grid-template-rows: auto auto min-content;
    padding: 75px 50px 104px;
    min-width: 1050px;

    &::before {
      top: 138px;
      left: 113px;
    }
  }

  .contacts__title {
    grid-column: 2 / 4;
    grid-row: 1 / 2;
    margin-top: 82px;
    margin-bottom: 35px;

    @include font-face(700, 53px, 57px);
    color: $black;
  }

  .contacts__list {
    grid-column: 2 / 4;
    grid-row: 2 / 3;
    display: flex;
    flex-direction: column;
    margin: 0;
    margin-bottom: 53px;
  }

  .contacts__value {
    padding: 0;
    margin: 0;
  }

  .contacts__value a {
    &::before {
      bottom: -9px;
    }
  }

  .contacts__map {
    grid-column: 4 / -1;
    grid-row: 1 / -1;
    min-width: 527px;
    min-height: 457px;
    margin-bottom: 0;

    &::before {
      top: 146px;
      left: 238px;
    }
  }

  .contacts__button-link {
    justify-self: start;
    grid-column: 2 / 4;
    grid-row: -1 / -2;
  }
}
