.product-video {
  width: 100%;

  &:hover {
    box-shadow: 0 6px 10px $o-black;
  }

  &:active {
    box-shadow: 0 3px 5px $o-black;
  }
}

.product-video__title {
  position: relative;
  display: flex;
  align-items: center;
  padding: 0;
  margin: 23px 13px 24px 90px;

  @include font-face(700, 17px, 24px);
  color: $black;

  &::before {
    content: "";
    position: absolute;
    left: -57px;
    top: 12px;
    width: 32px;
    height: 22px;
    background-image: url("../img/catalog/product-video/video.svg");
    background-repeat: no-repeat;
  }
}

.product-video__clip-wrapper {
  position: relative;
  min-width: 258px;
  min-height: 149px;
  background-color: $w-gray;
}

.product-video__clip-wrapper img {
  width: 100%;
  height: 100%;
  display: block;
}

.product-video__clip-wrapper button {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 101px;
  height: 77px;
  margin: 0;
  padding: 0;
  background-image: url("../img/catalog/product-video/play.svg");
  background-color: transparent;
  background-repeat: no-repeat;
  background-size: cover;
  border: none;
  cursor: pointer;

  &:hover {
    width: 121px;
    height: 92px;
  }

  &:active {
    width: 81px;
    height: 62px;
    opacity: 0.3;
  }
}

.product-video__column-wrapper {
  padding: 39px 32px 34px;
}

.product-video__text {
  padding: 0;
  margin: 0;

  @include font-face(400, 13px, 24px);
}

.product-video__button-link {
  margin: 0;
  margin-top: 41px;

  @include font-face(700, 18px, 16px);
}

@media (min-width: $tablet-width) {
  .product-video {
    display: grid;
  }

  .product-video__title {
    grid-row: 2 / 3;
    padding: 0;
    margin-top: 41px;
    margin-left: 107px;
    margin-bottom: 24px;
    min-height: 24px;

    @include font-face(700, 20px, 24px);

    &::before {
      left: -66px;
      top: -3px;
    }
  }

  .product-video__clip-wrapper {
    grid-row: 1 / 2;
    height: 455px;
  }

  .product-video__column-wrapper {
    padding: 0;
  }

  .product-video__text {
    padding: 0;
    margin: 0 40px 40px;

    @include font-face(400, 15px, 30px);
  }

  .product-video__button-link {
    margin: 0 41px 45px;
    padding-top: 16px;
    padding-bottom: 16px;

    @include font-face(700, 17px, 16px);
  }
}

@media (min-width: $desktop-width) {
  .product-video {
    display: grid;
    grid-template-columns: auto auto auto auto auto auto;
    grid-template-rows: min-content min-content;
  }

  .product-video__title {
    display: block;
    grid-column: -4 / -1;
    grid-row: 1 / 2;
    margin: 98px 60px 25px 80px;
    padding: 0;
    min-height: 24px;

    @include font-face(700, 20px, 24px);
    border: none;

    &::before {
      left: 0;
      top: -50px;
    }
  }

  .product-video__clip-wrapper {
    grid-row: 1 / -1;
    grid-column: 1 / 4;
    width: 681px;
    margin-bottom: 0;
  }

  .product-video__column-wrapper {
    grid-column: -4 / -1;
    grid-row: 2 / 3;
    padding: 0 57px 60px 80px;
  }

  .product-video__text {
    margin: 0;
    margin-bottom: 51px;

    @include font-face(400, 15px, 30px);
  }

  .product-video__button-link {
    margin: 0;

    @include font-face(700, 17px, 16px);
  }
}
